import { Card, Grid, Group, Input, InputWrapper, TextInput } from "@mantine/core";
import { useContext, useEffect, useState } from "react";
import { ParamsStateContext } from "./ParamsProvider";

export const ParamsGroup = ({ namespace, subNamespace }) => {
    return (
        <Card
            sx={(theme) => ({
                backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[5] : theme.colors.gray[1]
            })}
            p={"xs"}
        >
            <Card.Section
                sx={(theme) => ({
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.bluegray[8] : theme.colors.bluegray[3]
                })}
            >
                <div style={{ height: 30, display: "flex", justifyContent: "center", alignItems: "center", color: "white", letterSpacing: 1, fontSize: 12 }}>
                    {namespace}
                </div>
            </Card.Section>

            <Grid gutter={10} style={{ textAlign: "left", marginTop: 5 }}>
                {
                    Object.entries(subNamespace).map(([sns, value]) =>
                        <Grid.Col span={6}>
                            <ParamsEntry ns={namespace} key={`params-sns-${sns}`} sns={sns} value={value} />
                        </Grid.Col>
                    )
                }
            </Grid>
        </Card>
    );
}

const ParamsEntry = ({ ns, sns, value }) => {

    const [v, setV] = useState(value);
    const paramsContext = useContext(ParamsStateContext)

    useEffect(() => {
        if (!paramsContext?.data) return;

        if (paramsContext.data[ns][sns] !== v)
            setV(paramsContext.data[ns][sns]);
    }, [paramsContext.data])

    const onChange = (e) => {
        const parsedInt = parseInt(e.target.value);
        if (!isNaN(parsedInt)) {
            setV(parsedInt);
            paramsContext.mutate(ns, sns, parsedInt);
        }
    }

    return (
        <div style={{ marginTop: 5 }}>
            <TextInput
                description={sns}
                size={"xs"}
                value={v}
                variant="default"
                onChange={(e) => onChange(e)}
            />
        </div>
    )
}