import { Button, Card, Group, Title, Tooltip } from "@mantine/core";
import { showNotification } from "@mantine/notifications";
import { useContext, useEffect, useState } from "react";
import { EventStateContext } from "./EventProvider";
import { ParamsStateContext } from "./ParamsProvider";

export const StorySection = () => {

    const eventContext = useContext(EventStateContext);
    const paramContext = useContext(ParamsStateContext);

    const [textCounter, setTextCounter] = useState(-1);

    const [storyText, setStoryText] = useState();

    const paramsContext = useContext(ParamsStateContext);

    const setupText = (index) => {
        if (!eventContext.currentEventNode) return;

        if (!eventContext.currentEventNode.texts[index]) {
            setStoryText("End.")
        }
        else {
            const text = eventContext.currentEventNode.texts[index];
            if (!text.prereq || reqChecker(text.prereq))
                setStoryText(text);
            else {
                nextText();
                return;
            }
        }
    }

    useEffect(() => {
        if (!eventContext.currentEventNode) return;
        console.log("set text counter");
        setTextCounter(0);
        setupText(0);
    }, [eventContext.currentEventNode]);

    useEffect(() => {
        setupText(textCounter);
    }, [textCounter]);

    const nextText = () => {
        setTextCounter(prev => prev + 1);
    }

    const resolveEffect = (choice) => {

        if (choice.conseq) {
            choice.conseq.forEach(c => resolveEffectSingle(c));
        }

        eventContext.loadEventNodeById(choice.next);
    }

    const reqChecker = (choiceReq) => {
        if (!choiceReq) return true;

        /*
        req:{
            domain: string,     <- namespace
            attr: string   <- subnamespace
            op: string    <- > / < / = / ...
            value: string|int   <- anything want to compare
        }
        */

        return choiceReq.every(req => reqCheckerSingle(req));
    }

    const reqCheckerSingle = (choiceReq) => {
        const targetedParams = paramsContext.data[choiceReq.domain]?.[choiceReq.attr];

        if (targetedParams === undefined) {
            console.log("Error: Domain / Attr not found.");
            return true;
        }

        switch (choiceReq.op) {
            case "GT": return targetedParams > choiceReq.value;
            case "LT": return targetedParams < choiceReq.value;
            case "GET": return targetedParams >= choiceReq.value;
            case "LET": return targetedParams <= choiceReq.value;
            case "ET": return targetedParams === choiceReq.value;
            case "NET": return targetedParams !== choiceReq.value;
            default: return true;
        };
    }

    const resolveEffectSingle = (conseq) => {
        const currentParamValue = paramsContext.data[conseq.domain][conseq.attr];

        switch (conseq.op) {
            case "A":
                paramsContext.mutate(conseq.domain, conseq.attr, currentParamValue + conseq.value);
                showNotification({ message: `${conseq.domain}.${conseq.attr} became ${currentParamValue + conseq.value}(+${conseq.value})` });
                break;
            case "S":
                paramsContext.mutate(conseq.domain, conseq.attr, currentParamValue - conseq.value);
                showNotification({ message: `${conseq.domain}.${conseq.attr} became ${currentParamValue - conseq.value}(-${conseq.value})` });
                break;
            case "M":
                paramsContext.mutate(conseq.domain, conseq.attr, currentParamValue * conseq.value);
                showNotification({ message: `${conseq.domain}.${conseq.attr} became ${currentParamValue * conseq.value}(*${conseq.value})` });
                break;
            case "O":
                paramsContext.mutate(conseq.domain, conseq.attr, conseq.value);
                showNotification({ message: `${conseq.domain}.${conseq.attr} became ${conseq.value}(Overriden)` });
                break;
            case "T": showNotification({ message: `${conseq.domain}.${conseq.attr}.${conseq.value} will be triggered.` }); break;
            default: break;
        };
    }

    return (
        <Card shadow="sm" p="xs" withBorder style={{ height: "100%" }}>
            <Card.Section
                sx={(theme) => ({
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.bluegray[9] : theme.colors.bluegray[4]
                })}
            >
                <div style={{ height: 40, display: "flex", justifyContent: "center", alignItems: "center", color: "white", letterSpacing: 1 }}>
                    USER INTERFACE
                </div>
            </Card.Section>
            <Card
                shadow="sm" p="xs" withBorder style={{ height: "95%", width: "auto", marginTop: 10 }}
                sx={(theme) => ({
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[5] : theme.colors.gray[0]
                })}
            >
                {
                    eventContext.currentEventNode && storyText &&
                    <div style={{ display: "flex", flexDirection: "column", justifyContent: "space-between", height: "100%" }}>
                        <StoryText currentNodeId={eventContext.currentEventNode.id} currentText={storyText} />
                        <ChoiceGroup choices={textCounter === eventContext.currentEventNode.texts.length - 1 ? eventContext.currentEventNode.choices : null} nextFunc={nextText} effectFunc={resolveEffect} />
                    </div>
                }
            </Card>
        </Card >
    );
}

const StoryText = ({ currentNodeId, currentText }) => {

    const reqTextGenerator = (choiceReq) => {
        if (!choiceReq) return null;

        /*
        req:{
            domain: string,     <- namespace
            attr: string   <- subnamespace
            op: string    <- > / < / = / ...
            value: string|int   <- anything want to compare
        }
        */

        return choiceReq.map(req => reqTextGeneratorSingle(req)).join() + " ";
    }

    const reqTextGeneratorSingle = (choiceReq) => {
        let opText = "";

        switch (choiceReq.op) {
            case "GT": opText = ">"; break;
            case "LT": opText = "<"; break;
            case "GET": opText = "≥"; break;
            case "LET": opText = "≤"; break;
            case "ET": opText = "="; break;
            case "NET": opText = "≠"; break;
            default: return true;
        };

        return `　[${choiceReq.attr} ${opText} ${choiceReq.value}]`;
    }

    return (<div style={{ width: "90%", margin: "0 auto", marginTop: 20 }}>
        <Title order={1}>{currentNodeId}</Title>
        {
            currentText?.prereq &&
            <div style={{ width: "90%", margin: "20px auto", maxWidth: 700, color: "#40C057" }}>
                {reqTextGenerator(currentText.prereq)}
            </div>
        }
        <div style={{ width: "90%", margin: "20px auto", maxWidth: 700 }}>
            {currentText.text}
        </div>
    </div>)
}

const ChoiceGroup = ({ choices, nextFunc, effectFunc }) => {

    const paramsContext = useContext(ParamsStateContext);

    const [choiceButtons, setChoiceButtons] = useState(null);

    useEffect(() => {
        console.log(choices);
        renderChoices();
    }, [choices])

    useEffect(() => {
        renderChoices();
    }, [paramsContext.data]);

    const reqChecker = (choiceReq) => {
        if (!choiceReq) return true;

        /*
        req:{
            domain: string,     <- namespace
            attr: string   <- subnamespace
            op: string    <- > / < / = / ...
            value: string|int   <- anything want to compare
        }
        */

        return choiceReq.every(req => reqCheckerSingle(req));
    }

    const reqCheckerSingle = (choiceReq) => {
        const targetedParams = paramsContext.data[choiceReq.domain]?.[choiceReq.attr];
        console.log(`${choiceReq.domain}.${choiceReq.attr}: ${targetedParams} | Check: ${reqTextGeneratorSingle(choiceReq)}`);

        if (targetedParams === undefined) {
            console.log("Error: Domain / Attr not found.");
            return true;
        }

        switch (choiceReq.op) {
            case "GT": return targetedParams > choiceReq.value;
            case "LT": return targetedParams < choiceReq.value;
            case "GET": return targetedParams >= choiceReq.value;
            case "LET": return targetedParams <= choiceReq.value;
            case "ET": return targetedParams === choiceReq.value;
            case "NET": return targetedParams !== choiceReq.value;
            default: return true;
        };
    }

    const reqTextGenerator = (choiceReq) => {
        if (!choiceReq) return null;

        /*
        req:{
            domain: string,     <- namespace
            attr: string   <- subnamespace
            op: string    <- > / < / = / ...
            value: string|int   <- anything want to compare
        }
        */

        return choiceReq.map(req => reqTextGeneratorSingle(req)).join() + " ";
    }

    const reqTextGeneratorSingle = (choiceReq) => {
        let opText = "";

        switch (choiceReq.op) {
            case "GT": opText = ">"; break;
            case "LT": opText = "<"; break;
            case "GET": opText = "≥"; break;
            case "LET": opText = "≤"; break;
            case "ET": opText = "="; break;
            case "NET": opText = "≠"; break;
            default: return true;
        };

        return `　[${choiceReq.attr} ${opText} ${choiceReq.value}]`;
    }

    const conseqTextGenerator = (choiceConseq) => {
        if (!choiceConseq) return null;

        /*
        conseq:{
            domain: string,     <- namespace
            attr: string   <- subnamespace
            op: string    <- + / - / * / ...
            value: string|int   <- anything want to compare
        }
        */

        return <>{choiceConseq.map((req, index) => <div key={`conseq-${index}`}>{conseqTextGeneratorSingle(req)}</div>)}</>;
    }

    const conseqTextGeneratorSingle = (choiceConseq) => {
        switch (choiceConseq.op) {
            case "A": return `${choiceConseq.domain}.${choiceConseq.attr} + ${choiceConseq.value}`;
            case "S": return `${choiceConseq.domain}.${choiceConseq.attr} - ${choiceConseq.value}`;
            case "M": return `${choiceConseq.domain}.${choiceConseq.attr} * ${choiceConseq.value}`;
            case "O": return `${choiceConseq.domain}.${choiceConseq.attr} => ${choiceConseq.value}`;
            case "T": return `Trigger ${choiceConseq.domain}.${choiceConseq.attr}.${choiceConseq.value}`;
            default: return `${choiceConseq.domain}.${choiceConseq.attr} ?unknown? ${choiceConseq.value}`;
        };
    }

    const renderChoices = () => {
        if (choices == null) {
            setChoiceButtons(
                <>
                    <Button
                        onClick={nextFunc}
                        disabled={false}
                        color="green"
                        variant="light"
                        fullWidth
                    >
                        Next
                    </Button>
                </>
            )
        }
        else {
            setChoiceButtons(
                choices.map((choice, index) => {
                    const prereq = reqChecker(choice.prereq);
                    const req = reqChecker(choice.req);
                    const reqText = reqTextGenerator(choice.req);
                    const conseqText = conseqTextGenerator(choice.conseq);

                    return (
                        prereq &&
                        <Tooltip
                            label={conseqText ?? "No consequence"}
                            withArrow
                            style={{ width: "100%" }}
                            key={`choice-button-${index}`}
                        >
                            <Button
                                style={{ width: "100%" }}
                                onClick={() => effectFunc(choice)}
                                disabled={!req}
                                key={choice.text}
                                color="green"
                                variant="light"
                            >
                                <span style={{ color: "grey" }}><i>{reqText}</i></span>
                                <span><b>{choice.text}</b></span>
                            </Button>
                        </Tooltip>
                    )
                })
            )
        }
    }

    return (
        <Group direction="column" spacing={"xs"}>
            {choiceButtons}
        </Group>
    );
}