import { ActionIcon, Card, Group, LoadingOverlay, TextInput } from "@mantine/core";
import { useDebouncedValue } from "@mantine/hooks";
import { showNotification } from "@mantine/notifications";
import React, { useContext, useEffect, useState } from "react";
import { Edit } from "tabler-icons-react";
import { EventStateContext } from "../EventProvider";
import { CustomNodeWarningLoader } from "./EditEventNodeSection";
export const EventNodeMetadataTab = () => {

    const eventContext = useContext(EventStateContext);

    const [localEventNodeIdState, setLocalEventNodeIdState] = useState(eventContext.currentEventNode?.id.split('_').slice(1).join('_'));
    const [localEventNodeNextState, setLocalEventNodeNextState] = useState(eventContext.currentEventNode?.next);
    const [dEventNodeIdState] = useDebouncedValue(localEventNodeIdState, 200);
    const [dEventNodeNextState] = useDebouncedValue(localEventNodeNextState, 200);

    const onSaveNodeId = () => {
        const fullId = `${eventContext.currentEventId}_${dEventNodeIdState}`;

        if (Object.entries(eventContext.eventNodes).find(([key, value]) => key === fullId)) {
            showNotification({
                message: "Node ID already exists, rename failed.",
                color: "red"
            });
        }
        else {
            eventContext.onChangeEventNodeId(dEventNodeIdState);
            showNotification({
                message: "Node ID renamed successfully.",
                color: "green"
            });
        }
    }

    useEffect(() => {
        if (!dEventNodeNextState) return;
        eventContext.onChangeEventNodeNextNode(dEventNodeNextState);
    }, [dEventNodeNextState]);

    useEffect(() => {
        if (!eventContext.currentEventNode?.id) return;
        setLocalEventNodeIdState(eventContext.currentEventNode?.id.split('_').slice(1).join('_'));
    }, [eventContext.currentEventNode?.id])

    useEffect(() => {
        if (!eventContext.currentEventNode?.next) return;
        setLocalEventNodeNextState(eventContext.currentEventNode?.next);
    }, [eventContext.currentEventNode?.next])

    return (
        <Card shadow="sm" p="sm" withBorder style={{ height: "100%", flexBasis: "50%" }}>
            <Card.Section
                sx={(theme) => ({
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.bluegray[9] : theme.colors.bluegray[4]
                })}
            >
                <div style={{ height: 40, display: "flex", justifyContent: "center", alignItems: "center", color: "white", letterSpacing: 1 }}>
                    NODE METADATA
                </div>
            </Card.Section>
            <Group grow style={{ height: "100%" }}>
                <TextInput label={"Parent ID"} size="xs" value={eventContext.currentEventNode?.id.split("_")[0]} disabled />
                <TextInput label={"Event Node ID"} size="xs" value={localEventNodeIdState} onChange={(e) => setLocalEventNodeIdState(e.target.value)}
                    rightSection={<ActionIcon size="md" color={"bluegray"} variant="filled" onClick={onSaveNodeId} style={{ marginRight: -10, borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }}><Edit /></ActionIcon>}
                />
                <TextInput label={"Next Node"} size="xs" value={localEventNodeNextState} onChange={(e) => setLocalEventNodeNextState(e.target.value)} />
            </Group>
            <LoadingOverlay loader={<CustomNodeWarningLoader />} visible={!eventContext.currentEventNode} />
        </Card>

    )
}