import { Button, Card, Group, ScrollArea, Text, Tooltip, useMantineTheme } from "@mantine/core";
import { useClickOutside } from "@mantine/hooks";
import React, { useContext } from "react";
import { Trash, TrashX } from "tabler-icons-react";
import { EventStateContext } from "./EventProvider";

export const EventSection = ({ isEditMode }) => {

    const eventContext = useContext(EventStateContext);
    const theme = useMantineTheme();

    return (
        <Card shadow="sm" p="xs" withBorder style={{ height: "100%" }}>
            <Card.Section
                sx={(theme) => ({
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.bluegray[9] : theme.colors.bluegray[4]
                })}
            >
                <div style={{ height: 40, display: "flex", justifyContent: "center", alignItems: "center", color: "white", letterSpacing: 1 }}>
                    EVENTS
                </div>
            </Card.Section>
            <div style={{ position: "relative", width: "100%", height: "96%", marginTop: 10 }}>
                {
                    eventContext.events &&
                    <ScrollArea
                        style={{ position: "absolute", top: 4, bottom: 10, left: 0, right: -10 }}
                        offsetScrollbars
                        classNames={{
                            viewport: 'viewport-override',
                        }}
                    >
                        <Group direction="column" grow spacing={"xs"} style={{ width: "100%" }}>
                            {
                                Object.entries(eventContext.events).map(([key, value]) =>
                                    <EventSelectButton id={key} key={`event-select-button-${key}`} selected={key === eventContext.currentEventId} onSelect={eventContext.loadEventById} onDelete={eventContext.onDeleteEventOnly} onDeleteWithAllNodes={eventContext.onDeleteEventWithAllNodes} />
                                )
                            }
                        </Group>
                        {
                            isEditMode && <Button onClick={() => eventContext.addEvent()} variant="light" fullWidth color={"bluegray"} style={{ textAlign: "center", marginTop: 10 }} compact>+</Button>
                        }
                    </ScrollArea>
                }
            </div>
        </Card>
    )
}

const EventSelectButton = ({ id, selected, onSelect, onDelete, onDeleteWithAllNodes }) => {
    const theme = useMantineTheme();
    const [contextMenuOpen, setContextMenuOpen] = React.useState(false);
    const tooltipRef = useClickOutside(() => setContextMenuOpen(false));

    const onClickDeleteButton = (id) => {
        setContextMenuOpen(false);
        onDelete(id);
    }

    const onClickDeleteWithAllNodesButton = (id) => {
        setContextMenuOpen(false);
        onDeleteWithAllNodes(id);
    }

    const handleContextMenu = (e) => {
        e.preventDefault();
        setContextMenuOpen(!contextMenuOpen);
    }

    return (
        <Tooltip
            tooltipRef={tooltipRef}
            style={{ width: "100%" }}
            position="right"
            placement="center"
            allowPointerEvents
            withArrow
            opened={contextMenuOpen}
            zIndex={1000}
            styles={{
                arrow: {
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[4] : theme.colors.gray[2],
                    border: theme.colorScheme === "light" ? "1px solid #ced4da" : undefined
                },
                body: {
                    padding: 5,
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[4] : theme.colors.gray[2],
                    border: theme.colorScheme === "light" ? "1px solid #ced4da" : undefined
                },
            }}
            label={
                <Group direction="column" spacing={5} grow>
                    <Button size="xs" variant="light" color="red" leftIcon={<Trash size={14} />} onClick={() => onClickDeleteButton(id)}>Delete event only</Button>
                    <Button size="xs" variant="light" color="red" leftIcon={<TrashX size={14} />} onClick={() => onClickDeleteWithAllNodesButton(id)}>Delete w/ nodes</Button>
                </Group>
            }
        >
            <Button
                onContextMenu={handleContextMenu}
                onClick={() => onSelect(id)}
                fullWidth
                compact
                sx={(theme) => ({
                    backgroundColor: selected ? theme.colors.bluegray[7] : (theme.colorScheme === "dark" ? theme.colors.dark[5] : "#fff"),
                    '&:hover': {
                        backgroundColor: selected ? (theme.colorScheme === "dark" ? theme.colors.bluegray[6] : theme.colors.bluegray[8]) : (theme.colorScheme === "dark" ? theme.colors.dark[4] : "#f8f9fa"),
                    },
                    border: !selected && theme.colorScheme === "light" ? "1px solid #ced4da" : undefined
                })}
            >
                <Text
                    sx={(theme) => ({
                        color: selected ? "#fff" : (theme.colorScheme === "dark" ? theme.colors.dark[1] : theme.colors.bluegray[3]),
                    })}
                >
                    {id}
                </Text>
            </Button>
        </Tooltip>
    )
}