import { Button, Grid, Group, Title } from "@mantine/core";
import React, { useContext, useState } from "react";
import { NewSection } from "tabler-icons-react";
import { EditEventNodeSection } from "../components/EditEventNodeSection/EditEventNodeSection";
import { EventNodeSection } from "../components/EventNodeSection";
import { EventStateContext } from "../components/EventProvider";

import { EventSection } from "../components/EventSection";
import { JsonDrawer } from "../components/JsonDrawer";
import { ThemeChangeButton } from "../components/ThemeChangeButton";

export const EventEditingPage = ({ visible }) => {

    const eventContext = useContext(EventStateContext);

    const [openImportEventJsonDrawer, setOpenImportEventJsonDrawer] = useState(false);
    const [openImportEventNodesJsonDrawer, setOpenImportEventNodesJsonDrawer] = useState(false);
    const [openExportEventJsonDrawer, setOpenExportEventJsonDrawer] = useState(false);
    const [openExportEventNodesJsonDrawer, setOpenExportEventNodesJsonDrawer] = useState(false);

    return (
        <>
            <div style={{ display: visible ? "initial" : "none" }}>
                <div style={{ display: "flex", flexDirection: "column", width: "100%", height: "100%" }}>
                    <div style={{ display: "flex", justifyContent: "space-between", marginBottom: 10 }}>
                        <Title
                            order={2}
                            align="left"
                        >
                            CODE:A - EVENT EDITOR
                        </Title>
                        <Group spacing={"xs"}>
                            {/* <Button leftIcon={<FileUpload />} variant="light" color={"bluegray"} onClick={() => setOpenImportEventJsonDrawer(true)}>Import Event JSON</Button>
                            <Button leftIcon={<FileUpload />} variant="light" color={"bluegray"} onClick={() => setOpenImportEventNodesJsonDrawer(true)}>Import Events Nodes JSON</Button>
                            <Button leftIcon={<FileDownload />} variant="light" color={"bluegray"} onClick={() => setOpenExportEventJsonDrawer(true)}>Export Events JSON</Button>
                            <Button leftIcon={<FileDownload />} variant="light" color={"bluegray"} onClick={() => setOpenExportEventNodesJsonDrawer(true)}>Export Event Nodes JSON</Button>
                             */}
                            <Button leftIcon={<NewSection />} variant="light" color={"green"} onClick={eventContext.newSession}>New</Button>
                            <ThemeChangeButton />
                        </Group>
                    </div>
                    {
                        eventContext.events &&
                        <Grid gutter="xs" columns={12} style={{ height: "100%" }}>
                            <Grid.Col span={1}>
                                <EventSection isEditMode />
                            </Grid.Col>
                            <Grid.Col span={1}>
                                <EventNodeSection isEditMode />
                            </Grid.Col>
                            <Grid.Col span={10}>
                                <EditEventNodeSection />
                            </Grid.Col>
                        </Grid>
                    }
                </div>
            </div>

            <JsonDrawer
                title={"Import Event JSON"}
                actionButtonText={"Import"}
                open={openImportEventJsonDrawer}
                setOpen={setOpenImportEventJsonDrawer}
                onClickActionButton={eventContext.setImportedEventJson}
            />
            <JsonDrawer
                title={"Import Event Node JSON"}
                actionButtonText={"Import"}
                open={openImportEventNodesJsonDrawer}
                setOpen={setOpenImportEventNodesJsonDrawer}
                onClickActionButton={eventContext.setImportedEventNodesJson}
            />
            <JsonDrawer
                title={"Export Event JSON"}
                actionButtonText={"Copy"}
                initValue={JSON.stringify(eventContext.events, null, 2)}
                open={openExportEventJsonDrawer}
                setOpen={setOpenExportEventJsonDrawer}
                onClickActionButton={() => navigator.clipboard.writeText(JSON.stringify(eventContext.events, null, 2))}
            />
            <JsonDrawer
                title={"Export Event Node JSON"}
                actionButtonText={"Copy"}
                initValue={JSON.stringify(eventContext.eventNodes, null, 2)}
                open={openExportEventNodesJsonDrawer}
                setOpen={setOpenExportEventNodesJsonDrawer}
                onClickActionButton={() => navigator.clipboard.writeText(JSON.stringify(eventContext.eventNodes, null, 2))}
            />
        </>
    )
}