import { ActionIcon, Group, Paper, Space, Tooltip, useMantineTheme } from "@mantine/core";
import React from "react";
import { CalendarEvent, ChartDots3, FileImport, Folder, TrashX, Variable } from "tabler-icons-react";

export const WorkSessionEntry = ({ timestamp, data, numOfParams, numOfEvents, numOfEventNodes, onLoad, onDelete }) => {

    const theme = useMantineTheme();

    const convertTimestamp = (timestamp) => {
        const date = new Date(timestamp);

        const options = { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric', second: 'numeric', hour12: false };
        return date.toLocaleString("en-US", options);
    }

    return (
        <Paper shadow="xs" p="xs" withBorder>
            <Group spacing={5} position="apart">
                <Group direction="column" spacing={5}>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "space-between", gap: 10 }}>
                        <Folder />
                        <div style={{ fontSize: 12, textAlign: "left", color: "#666", display: "flex", alignItems: "center" }}>
                            <Variable size={18} color={theme.colors.gray[6]} />
                            <Space w={2} />
                            {numOfParams}
                        </div>
                        <div style={{ fontSize: 12, textAlign: "left", color: "#666", display: "flex", alignItems: "center" }}>
                            <CalendarEvent size={18} color={theme.colors.gray[6]} />
                            <Space w={2} />
                            {numOfEvents}
                        </div>
                        <div style={{ fontSize: 12, textAlign: "left", color: "#666", display: "flex", alignItems: "center" }}>
                            <ChartDots3 size={18} color={theme.colors.gray[6]} />
                            <Space w={2} />
                            {numOfEventNodes}
                        </div>
                    </div>
                    <div style={{ fontSize: 12, textAlign: "left", color: "#666" }}>{convertTimestamp(timestamp)}</div>
                </Group>
                <Group direction="column" spacing={5}>
                    <Tooltip
                        label={"Load Session"}
                        withArrow
                        position="right"
                        placement="center"
                        radius={"sm"}
                        transition="scale"
                    >
                        <ActionIcon variant="light" color={"bluegray"} size="sm" onClick={() => onLoad(data)}>
                            <FileImport />
                        </ActionIcon>
                    </Tooltip>

                    <Tooltip
                        label={"Delete Session"}
                        withArrow
                        position="right"
                        placement="center"
                        radius={"sm"}
                        transition="scale"
                    >
                        <ActionIcon variant="light" color={"bluegray"} size="sm" onClick={() => onDelete(timestamp)}>
                            <TrashX />
                        </ActionIcon>
                    </Tooltip>
                </Group>
            </Group>
        </Paper>
    )
}
