import { ActionIcon, Card, ColorSchemeProvider, Group, MantineProvider, Tooltip } from '@mantine/core';
import { NotificationsProvider } from '@mantine/notifications';
import React, { useEffect, useState } from 'react';
import { Database, Edit, TestPipe } from 'tabler-icons-react';
import './App.css';
import { EventProvider } from './components/EventProvider';
import { ParamsProvider } from './components/ParamsProvider';
import { EventEditingPage } from './pages/EventEditingPage';
import { EventSimulationPage } from './pages/EventSimulationPage';
import { IOPage } from './pages/IOPage';

function App() {

  const [page, setPage] = useState(0);

  useEffect(() => { console.log(page) }, [page])

  const [colorScheme, setColorScheme] = useState('dark');
  const toggleColorScheme = (value) =>
    setColorScheme(value || (colorScheme === 'dark' ? 'light' : 'dark'));

  return (
    <div className='App'>
      <ColorSchemeProvider colorScheme={colorScheme} toggleColorScheme={toggleColorScheme}>
        <MantineProvider
          theme={{
            // Override any other properties from default theme
            colorScheme: colorScheme,
            fontFamily: 'Varela Round',
            headings: { fontFamily: 'Varela Round' },
            spacing: { xs: 10, sm: 20, md: 25, lg: 30, xl: 40 },
            colors: {
              bluegray: [
                '#e5ecf0',
                '#bfd1da',
                '#94b2c2',
                '#6993a9',
                '#487b96',
                '#286484',
                '#245c7c',
                '#1e5271',
                '#184867',
                '#0f3654'
              ],
            },
            primaryColor: 'bluegray',
          }}
          withGlobalStyles
        >
          <NotificationsProvider>
            <EventProvider>
              <ParamsProvider>
                <div style={{ margin: 10, height: "97vh", display: "flex" }}>
                  <Card
                    style={{
                      minWidth: "max-content",
                      height: "100%",
                      marginRight: 10
                    }}
                    p={0}
                    shadow="lg"
                    withBorder
                  >
                    <Group
                      direction='column'
                      spacing={"xs"}
                      style={{ padding: 10 }}
                    >
                      <PageTabIcon name={"Edit Event"} pageNo={0} currentPage={page} icon={<Edit />} changePage={setPage} color={"bluegray"} />
                      <PageTabIcon name={"Simulate Event"} pageNo={1} currentPage={page} icon={<TestPipe />} changePage={setPage} color={"bluegray"} />
                      <PageTabIcon name={"Import / Export"} pageNo={2} currentPage={page} icon={<Database />} changePage={setPage} color={"bluegray"} />
                    </Group>
                  </Card>
                  <div style={{ flexGrow: 1 }}>
                    {page === 0 && <EventEditingPage visible={page === 0} />}
                    {page === 1 && <EventSimulationPage visible={page === 1} />}
                    {page === 2 && <IOPage visible={page === 2} />}
                  </div>
                </div>
              </ParamsProvider>
            </EventProvider>
          </NotificationsProvider>
        </MantineProvider >
      </ColorSchemeProvider>
    </div>
  );
}

const PageTabIcon = ({ name, pageNo, currentPage, icon, color, changePage }) => (
  <Tooltip
    label={name}
    withArrow
    position="right"
    placement="center"
    radius={"sm"}
    transition="scale"
  >
    <ActionIcon
      size={"xl"}
      variant={currentPage === pageNo ? "filled" : "default"}
      radius="md"
      onClick={() => changePage(pageNo)}
      color={color}
      sx={(theme) => ({
        color: currentPage === pageNo ? "#fff" : (theme.colorScheme === "dark" ? "#fff" : theme.colors.bluegray[5]),
      })}
    >
      {icon}
    </ActionIcon>
  </Tooltip>
)

export default App;
