import { Button, JsonInput } from "@mantine/core";
import { showNotification } from "@mantine/notifications";
import React, { useRef } from "react";

export const JsonInputWrapper = ({ initValue, buttonConfigs, disabled = false }) => {

    const jsonRef = useRef();

    const checkValidJson = () => {
        const json = jsonRef.current.value;
        if (json) return;

        try {
            const jsonObj = JSON.parse(json);
            return jsonObj;
        } catch (e) {
            showNotification({ message: "Invalid JSON.", color: "red" });
        }
    }

    return (
        <div style={{ display: "flex", flexDirection: "column", width: "100%", height: "calc(100% - 20px)", justifyContent: "space-between" }}>
            <JsonInput
                formatOnBlur
                autosize={false}
                fullWidth
                ref={jsonRef}
                defaultValue={initValue}
                disabled={disabled}
                styles={{
                    wrapper: { height: '100%' },
                    input: { height: '100%' },
                    root: { height: '88%' },
                }}
                onBlur={checkValidJson}
            >

            </JsonInput>
            {
                buttonConfigs.map((config, index) => {
                    return (
                        <Button style={{ marginTop: 2.5, marginBottom: 2.5 }} variant="light" color={"bluegray"} fullWidth onClick={() => config.action(jsonRef.current.value)}>{config.text}</Button>
                    )
                })
            }
        </div >
    )
};