import React, { useEffect, useState } from "react";
import { eventNodes, events } from "../components/events";
import useCallbackState from "../utils/UseCallbackState";

export const EventStateContext = React.createContext();

const newEventTemplate = (id) => ({
    [id]: {
        name: id,
        entry: id
    }
});

const newEventNodeTemplate = (id) => ({
    [id]: {
        id: id,
        texts: [],
        choices: [],
        next: ""
    }
});

const newChoiceTemplate = () => ({
    text: "",
    next: ""
});

const newChoiceOptionTemplate = () => ({
    domain: "",
    attr: "",
    op: "",
    value: 0
});

export const EventProvider = ({ children }) => {

    const [importedEventJson, setImportedEventJson] = useState(events);
    const [currentEventId, setCurrentEventId] = useCallbackState();

    const [importedEventNodesJson, setImportedEventNodesJson] = useCallbackState(eventNodes);
    const [currentEventNodeId, setCurrentEventNodeId] = useState();

    const [filteredEventNodes, setFilteredEventNodes] = useState();

    const loadWholeEventById = (id) => {
        if (!id) return;

        setCurrentEventId(id);
        const targetEntryNodeId = importedEventJson[id].entry;
        loadEventById(targetEntryNodeId);

        setNodeFilter(importedEventNodesJson, id);

        if (!importedEventNodesJson) return;
    }

    const loadEventById = (id) => {
        const targetNode = importedEventNodesJson[id];
        loadEvent(targetNode);
    }

    const loadEvent = (node) => {
        setCurrentEventNodeId(node);
    }

    const onChangeEventNodeText = (texts) => {
        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    texts: texts
                }
            })
        )
    }

    const onChangeEventName = (name) => {
        setImportedEventJson(prev => (
            {
                ...prev,
                [currentEventId]: {
                    ...prev[currentEventId],
                    name: name
                }
            })
        )
    }

    const onChangeEventEntry = (entry) => {

        const eventId = entry.split("_")[0];

        if (eventId !== currentEventId) {

            const jsonCopy = importedEventJson;

            delete Object.assign(jsonCopy, { [eventId]: jsonCopy[currentEventId] })[currentEventId];

            jsonCopy[eventId].entry = entry;

            setImportedEventJson(jsonCopy);
            setCurrentEventId(eventId);

            const bufferNodeJson = importedEventNodesJson;

            Object.entries(bufferNodeJson)
                .filter(([key, value]) => key.split("_")[0] === currentEventId)
                .forEach(([key, value]) => {
                    const namespaceArray = key.split("_");
                    namespaceArray[0] = eventId;
                    const newId = namespaceArray.join("_");
                    delete Object.assign(bufferNodeJson, { [newId]: bufferNodeJson[key] })[key];
                    bufferNodeJson[newId].id = newId;
                });

            console.log(bufferNodeJson);

            setImportedEventNodesJson(bufferNodeJson);
            setFilteredEventNodes(bufferNodeJson, eventId);
            setCurrentEventNodeId(undefined);
        }
        else {
            setImportedEventJson(prev => (
                {
                    ...prev,
                    [currentEventId]: {
                        ...prev[currentEventId],
                        entry: entry
                    }
                })
            )
        }
    }

    const onChangeEventNodeId = (newId) => {
        const combinatedId = currentEventId + "_" + newId;

        if (currentEventNodeId === combinatedId) return;

        const jsonCopy = importedEventNodesJson;

        delete Object.assign(jsonCopy, { [combinatedId]: jsonCopy[currentEventNodeId] })[currentEventNodeId];

        jsonCopy[combinatedId].id = combinatedId;

        setImportedEventNodesJson(jsonCopy);
        setNodeFilter(jsonCopy, currentEventId);
        setCurrentEventNodeId(combinatedId);
    }

    const onChangeEventNodeNextNode = (nextNode) => {
        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    next: nextNode
                }
            })
        )
    }

    const onChangeChoiceText = (index, text) => {

        const choicesBuffer = importedEventNodesJson[currentEventNodeId].choices;

        choicesBuffer[index].text = text;

        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    choices: choicesBuffer
                }
            }
        ));
    }

    const onChangeChoiceNext = (index, next) => {
        const choicesBuffer = importedEventNodesJson[currentEventNodeId].choices;

        choicesBuffer[index].next = next;

        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    choices: choicesBuffer
                }
            }
        ));
    }

    const onChangeChoiceOptionContent = (choiceIndex, optionType, optionIndex, contentType, value) => {
        const choicesBuffer = JSON.parse(JSON.stringify(importedEventNodesJson[currentEventNodeId].choices));

        choicesBuffer[choiceIndex][optionType][optionIndex][contentType] = value;

        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    choices: choicesBuffer
                }
            }
        ));
    }

    const onChangeTextOptionContent = (textIndex, optionType, optionIndex, contentType, value) => {
        const textsBuffer = JSON.parse(JSON.stringify(importedEventNodesJson[currentEventNodeId].texts));

        textsBuffer[textIndex][optionType][optionIndex][contentType] = value;

        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    texts: textsBuffer
                }
            }
        ));
    }

    const newSession = () => {
        addEvent(true);
    }

    const addEvent = (forceNew = false) => {
        const timestamp = Date.now().toString();

        if (!importedEventJson || forceNew === true) {
            setImportedEventJson(newEventTemplate(timestamp));
        }
        else {
            setImportedEventJson(prev => ({
                ...prev,
                ...newEventTemplate(timestamp)
            }));
        }

        addEventNode(timestamp, forceNew);

        return timestamp;

        //loadWholeEventById(timestamp);
    }

    const addEventNode = (id, forceNew = false) => {
        if (id === undefined) {
            id = currentEventId;
        }

        const timestamp = Date.now().toString();
        const temp_id = id + "_" + timestamp;

        if (!importedEventNodesJson || forceNew === true) {
            const newObject = { ...newEventNodeTemplate(temp_id) };
            setImportedEventNodesJson(newObject);

            setNodeFilter(newObject, id);
        }
        else {
            const newObject = {
                ...importedEventNodesJson,
                ...newEventNodeTemplate(temp_id)
            }

            setImportedEventNodesJson(newObject);

            setNodeFilter(newObject, id);
        }


        //loadEventById(temp_id);
    }

    const setNodeFilter = (eventNodes, id) => {

        setFilteredEventNodes(
            Object.keys(eventNodes)
                .filter((key) => key.split("_")[0] === id)
                .reduce((obj, key) => ({ ...obj, [key]: eventNodes[key] }), {}
                )
        );
    }

    const onDeleteEventOnly = (id) => {
        const jsonCopy = JSON.parse(JSON.stringify(importedEventJson));
        delete jsonCopy[id];

        setImportedEventJson(jsonCopy);

        if (currentEventId && currentEventId === id) {
            setCurrentEventId(undefined);
            setFilteredEventNodes([]);
        }
    }

    const onDeleteEventWithAllNodes = (id) => {
        onDeleteEventOnly(id);

        const jsonCopy = importedEventNodesJson;

        Object.entries(jsonCopy)
            .filter(([key, value]) => key.split("_")[0] === id)
            .forEach(([key, value]) => {
                delete jsonCopy[key];
            });


        setImportedEventNodesJson(jsonCopy);

        if (currentEventNodeId && currentEventNodeId.split("_")[0] === id)
            setCurrentEventNodeId(undefined);
    }

    const onDeleteEventNode = (id) => {
        const jsonCopy = JSON.parse(JSON.stringify(importedEventNodesJson));
        delete jsonCopy[id];

        setImportedEventNodesJson(jsonCopy, (value) => {
            setNodeFilter(value, id.split("_")[0]);
        });

        if (currentEventNodeId && currentEventNodeId === id) {
            setCurrentEventNodeId(undefined);
        }
    }

    const onAddChoice = () => {
        let choicesBuffer = importedEventNodesJson[currentEventNodeId].choices;

        if (!choicesBuffer) {
            choicesBuffer = [];
        }

        choicesBuffer.push(newChoiceTemplate());

        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    choices: choicesBuffer
                }
            }
        ));
    }

    const onDeleteChoice = (index) => {
        const nodeBuffer = importedEventNodesJson[currentEventNodeId];
        const choicesBuffer = importedEventNodesJson[currentEventNodeId].choices;

        choicesBuffer.splice(index, 1);

        if (choicesBuffer.length === 0) {
            delete nodeBuffer.choices;

            setImportedEventNodesJson(prev => (
                {
                    ...prev,
                    [currentEventNodeId]: nodeBuffer
                }
            ));
        }
        else {
            setImportedEventNodesJson(prev => (
                {
                    ...prev,
                    [currentEventNodeId]: {
                        ...prev[currentEventNodeId],
                        choices: choicesBuffer
                    }
                }
            ));
        }
    }

    const onAddChoiceOption = (choiceIndex, optionType) => {
        const choicesBuffer = importedEventNodesJson[currentEventNodeId].choices;

        if (!choicesBuffer[choiceIndex][optionType]) {
            choicesBuffer[choiceIndex][optionType] = [];
        }

        choicesBuffer[choiceIndex][optionType].push(newChoiceOptionTemplate());

        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    choices: choicesBuffer
                }
            }
        ));
    }

    const onDeleteChoiceOption = (choiceIndex, optionType, optionIndex) => {
        const choicesBuffer = importedEventNodesJson[currentEventNodeId].choices;

        choicesBuffer[choiceIndex][optionType].splice(optionIndex, 1);

        if (choicesBuffer[choiceIndex][optionType].length === 0) {
            delete choicesBuffer[choiceIndex][optionType];
        }

        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    choices: choicesBuffer
                }
            }
        ));
    }

    const onAddTextOption = (textIndex, optionType) => {
        const textsBuffer = importedEventNodesJson[currentEventNodeId].texts;

        if (!textsBuffer[textIndex][optionType]) {
            textsBuffer[textIndex][optionType] = [];
        }

        textsBuffer[textIndex][optionType].push(newChoiceOptionTemplate());

        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    texts: textsBuffer
                }
            }
        ));
    }

    const onDeleteTextOption = (textIndex, optionType, optionIndex) => {
        const textsBuffer = importedEventNodesJson[currentEventNodeId].texts;

        textsBuffer[textIndex][optionType].splice(optionIndex, 1);

        if (textsBuffer[textIndex][optionType].length === 0) {
            delete textsBuffer[textIndex][optionType];
        }

        setImportedEventNodesJson(prev => (
            {
                ...prev,
                [currentEventNodeId]: {
                    ...prev[currentEventNodeId],
                    texts: textsBuffer
                }
            }
        ));
    }

    const exportEventsLocalizedString = () => {
        const eventsBuffer = structuredClone(importedEventJson);

        Object.entries(eventsBuffer).forEach(([eventId, eventContent]) => {
            if (eventContent.name) {
                eventContent.name = `EVT_${eventId}_NAME`;
            }
        });

        return eventsBuffer;
    }

    const exportEventNodesLocalizedString = () => {
        const eventNodesBuffer = structuredClone(importedEventNodesJson);

        Object.entries(eventNodesBuffer).forEach(([eventNodeId, eventNodeContent]) => {
            if (eventNodeContent.texts) {
                eventNodeContent.texts.forEach((text, textIndex) => {
                    text.text = `EVTNODE_${eventNodeId}_TEXT_${textIndex}`;
                });
            }

            if (eventNodeContent.choices) {
                eventNodeContent.choices.forEach((choice, choiceIndex) => {
                    choice.text = `EVTNODE_${eventNodeId}_CHOICE_${choiceIndex}`;
                });
            }
        });

        return eventNodesBuffer;
    }

    useEffect(() => {
        console.log(importedEventJson);
    }, [importedEventJson])

    useEffect(() => {
        console.log(importedEventNodesJson);
    }, [importedEventNodesJson])

    return (
        <EventStateContext.Provider value={
            {
                // get
                events: importedEventJson,
                eventNodes: importedEventNodesJson,
                currentEventId: currentEventId,
                currentEventNodeId: currentEventNodeId,
                currentEvent: importedEventJson?.[currentEventId],
                currentEventNode: importedEventNodesJson?.[currentEventNodeId],
                filteredEventNodes: filteredEventNodes,

                // set
                newSession: newSession,
                loadEventById: loadWholeEventById,
                loadEventNodeById: setCurrentEventNodeId,
                addEvent: addEvent,
                addEventNode: addEventNode,
                onChangeEventName: onChangeEventName,
                onChangeEventEntry: onChangeEventEntry,
                onChangeEventNodeId: onChangeEventNodeId,
                onChangeEventNodeText: onChangeEventNodeText,
                onChangeEventNodeNextNode: onChangeEventNodeNextNode,
                onChangeChoiceText: onChangeChoiceText,
                onChangeChoiceNext: onChangeChoiceNext,
                onDeleteEventOnly: onDeleteEventOnly,
                onDeleteEventWithAllNodes: onDeleteEventWithAllNodes,
                onDeleteEventNode: onDeleteEventNode,
                onChangeChoiceOptionContent: onChangeChoiceOptionContent,
                onAddChoice: onAddChoice,
                onDeleteChoice: onDeleteChoice,
                onAddChoiceOption: onAddChoiceOption,
                onDeleteChoiceOption: onDeleteChoiceOption,
                onChangeTextOptionContent: onChangeTextOptionContent,
                onAddTextOption: onAddTextOption,
                onDeleteTextOption: onDeleteTextOption,


                setImportedEventJson: setImportedEventJson,
                setImportedEventNodesJson: setImportedEventNodesJson,

                // Export
                exportEventsLocalizedString: exportEventsLocalizedString,
                exportEventNodesLocalizedString: exportEventNodesLocalizedString
            }
        }>
            {children}
        </EventStateContext.Provider>
    );
}