import { Card } from "@mantine/core"
import React from "react"

export const CustomCard = ({ children, title }) => (
    <Card shadow="sm" p="xs" withBorder style={{ height: "100%" }}>
        <Card.Section
            sx={(theme) => ({
                backgroundColor: theme.colorScheme === "dark" ? theme.colors.bluegray[9] : theme.colors.bluegray[4]
            })}
        >
            <div style={{ height: 40, display: "flex", justifyContent: "center", alignItems: "center", color: "white", letterSpacing: 1 }}>
                {title}
            </div>
        </Card.Section>
        <div style={{ position: "relative", width: "100%", height: "96%", marginTop: 10 }}>
            {children}
        </div>
    </Card>
)