import React, { useState } from "react";

export const ParamsStateContext = React.createContext();

const statInit = {
    "player_props": {
        "max_hp": 50,
        "atk_eff": 0,
        "amr_eff": 0
    },
    "player_stats": {
        "philosophy": 3,
        "literature": 3,
        "visual_arts": 3,
        "performance_arts": 3,
        "history": 3,
        "law": 3
    },
    "endings": {
        "a": 0,
        "b": 0,
        "c": 0
    }
};

export const ParamsProvider = ({ children }) => {

    const [params, setParams] = useState(statInit);

    const mutate = (namespace, subNamespace, value) => {
        console.log("Mutate: " + namespace + " " + subNamespace + " " + value);

        setParams(prev => (
            {
                ...prev,
                [namespace]: {
                    ...prev[namespace],
                    [subNamespace]: value
                }
            }
        ));
    }

    const importNew = (json) => {
        setParams(json);
    }

    return (
        <ParamsStateContext.Provider value={{ data: params, mutate: mutate, importNew: importNew }}>
            {children}
        </ParamsStateContext.Provider>
    );
}