import { AlertTriangle } from "tabler-icons-react";
import { EventMetadataTab } from "./EventMetadataTab";
import { EventNodeChoicesTab } from "./EventNodeChoicesTab";
import { EventNodeMetadataTab } from "./EventNodeMetadataTab";
import { EventNodeTextsTab } from "./EventNodeTextsTab";

export const EditEventNodeSection = () => {

    return (
        <div style={{ display: "inline-flex", flexDirection: "column", gap: 10, width: "100%", height: "100%" }}>
            <div style={{ display: "inline-flex", gap: 10, flexGrow: 1, height: 150 }}>
                <EventMetadataTab />
                <EventNodeMetadataTab />
            </div>
            <div style={{ display: "inline-flex", gap: 10, flexGrow: 1, height: "100%" }}>
                <EventNodeTextsTab />
                <EventNodeChoicesTab />
            </div>
        </div>
    )
}

export const CustomNodeWarningLoader = ({ isEvent }) => {
    return (
        <div>
            <AlertTriangle />
            <div>{`Select or add a ${isEvent ? "event" : "node"} first!`}</div>
        </div >
    )
}