import { useContext } from "react";
import { ParamsGroup } from "./ParamsGroup.jsx"
import { StatStateContext } from "../App"
import { Card, Group, InputWrapper, ScrollArea } from "@mantine/core";
import { EventStateContext } from "./EventProvider.jsx";
import { ParamsStateContext } from "./ParamsProvider.jsx";

export const ParamsSection = () => {

    const eventContext = useContext(EventStateContext);
    const paramContext = useContext(ParamsStateContext);

    return (
        <Card shadow="sm" p="xs" withBorder style={{ height: "100%" }}>
            <Card.Section
                sx={(theme) => ({
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.bluegray[9] : theme.colors.bluegray[4]
                })}
            >
                <div style={{ height: 40, display: "flex", justifyContent: "center", alignItems: "center", color: "white", letterSpacing: 1 }}>
                    PARAMS
                </div>
            </Card.Section>
            <div style={{ position: "relative", width: "100%", height: "96%", marginTop: 10 }}>
                {
                    paramContext.data &&
                    <ScrollArea
                        style={{ position: "absolute", top: 4, bottom: 10, left: 0, right: -10 }}
                        offsetScrollbars
                        classNames={{
                            viewport: 'viewport-override',
                        }}
                    >
                        <Group direction="row" spacing={"xs"}>
                            {
                                paramContext.data && Object.entries(paramContext.data).map(([ns, sns]) =>
                                    <ParamsGroup namespace={ns} subNamespace={sns} key={`params-ns-${ns}`} />
                                )
                            }
                        </Group>
                    </ScrollArea>
                }
            </div>
        </Card>
    )
}