import { Button, Drawer, JsonInput, Title } from "@mantine/core";
import React, { useRef } from "react";

export const JsonDrawer = ({ title, open, setOpen, initValue, actionButtonText, onClickActionButton }) => {

    const jsonRef = useRef();

    return (
        <Drawer
            zIndex={1000}
            opened={open}
            position="right"
            onClose={() => setOpen(false)}
            title={
                <Title
                    order={3}
                    align="left"
                >
                    {title}
                </Title>
            }
            padding="xl"
            size="xl"
        >
            <div style={{ display: "flex", flexDirection: "column", width: "100%", height: "95%", justifyContent: "space-between" }}>
                <JsonInput
                    validationError="Invalid json"
                    formatOnBlur
                    autosize={false}
                    fullWidth
                    minRows={30}
                    maxRows={30}
                    ref={jsonRef}
                    defaultValue={initValue}
                >

                </JsonInput>
                <Button variant="light" color={"bluegray"} fullWidth onClick={() => { onClickActionButton(JSON.parse(jsonRef.current.value)); setOpen(false) }}>{actionButtonText}</Button>
            </div>
        </Drawer>
    )
};