import { ActionIcon, Autocomplete, Button, Card, Collapse, Group, LoadingOverlay, ScrollArea, Textarea, TextInput } from "@mantine/core";
import { useDebouncedValue } from "@mantine/hooks";
import { showNotification } from "@mantine/notifications";
import { useContext, useEffect, useState } from "react";
import { ArrowsSplit2, ChartArrowsVertical, Eye, HandClick, Pencil, X } from "tabler-icons-react";
import { EventStateContext } from "../EventProvider";
import { CustomNodeWarningLoader } from "./EditEventNodeSection";

export const EventNodeChoicesTab = () => {

    const eventContext = useContext(EventStateContext);

    return (
        <Card shadow="sm" p="lg" withBorder style={{ height: "100%", flexBasis: "50%" }}>
            <Card.Section
                sx={(theme) => ({
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.bluegray[9] : theme.colors.bluegray[4]
                })}
            >
                <div style={{ height: 40, display: "flex", justifyContent: "center", alignItems: "center", color: "white", letterSpacing: 1 }}>
                    NODE CHOICES
                </div>
            </Card.Section>

            <div style={{ position: "relative", height: "100%" }}>
                <ScrollArea offsetScrollbars style={{ position: "absolute", top: 20, bottom: 20, left: 10, right: 10 }}>
                    {
                        eventContext.currentEventNode?.choices && eventContext.currentEventNode.choices.map((choice, index) =>
                            <EventNodeChoiceEntry
                                choice={choice} key={`choice-entry-${index}`} index={index}
                                onChangeChoiceText={eventContext.onChangeChoiceText}
                                onChangeChoiceNext={eventContext.onChangeChoiceNext}

                                onDeleteChoice={eventContext.onDeleteChoice}

                                onAddChoiceOption={eventContext.onAddChoiceOption}
                                onDeleteChoiceOption={eventContext.onDeleteChoiceOption}

                                onChangeChoiceOptionContent={eventContext.onChangeChoiceOptionContent}
                            />
                        )
                    }
                    <Button fullWidth variant="light" compact color={"bluegray"} style={{ marginTop: eventContext.currentEventNode?.choices && eventContext.currentEventNode.choices.length > 0 ? 10 : 0 }} onClick={eventContext.onAddChoice}>+</Button>
                </ScrollArea>
            </div>

            <LoadingOverlay loader={<CustomNodeWarningLoader />} visible={!eventContext.currentEventNode} />
        </Card>
    )
}

const EventNodeChoiceEntry = ({ choice, index, onChangeChoiceText, onChangeChoiceNext, onDeleteChoice, onChangeChoiceOptionContent, onAddChoiceOption, onDeleteChoiceOption }) => {

    const [openDetail, setOpenDetail] = useState(false);
    const [tab, setTab] = useState(2);

    const [localChoiceTextState, setLocalChoiceTextState] = useState();
    const [localChoiceNextState, setLocalChoiceNextState] = useState();
    const [dChoiceTextState] = useDebouncedValue(localChoiceTextState, 200);
    const [dChoiceNextState] = useDebouncedValue(localChoiceNextState, 200);

    useEffect(() => {
        if (!dChoiceTextState || dChoiceTextState === choice.text) return;
        onChangeChoiceText(index, dChoiceTextState);
    }, [dChoiceTextState]);

    useEffect(() => {
        if (!dChoiceNextState || dChoiceNextState === choice.next) return;
        onChangeChoiceNext(index, dChoiceNextState);
    }, [dChoiceNextState]);

    useEffect(() => {
        if (!choice?.text || localChoiceTextState === choice.text) return;
        setLocalChoiceTextState(choice.text);
    }, [choice.text])

    useEffect(() => {
        if (!choice?.next || localChoiceNextState === choice.next) return;
        setLocalChoiceNextState(choice.next);
    }, [choice.next])

    return (
        <Card
            p={"xs"}
            sx={(theme) => ({
                backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[4] : theme.colors.gray[2],
                marginBottom: 5
            })}

        >
            <Group direction="column" grow spacing={"0px"}>
                <div style={{ display: "flex", gap: 4, alignItems: "center" }}>
                    <Button
                        compact
                        variant="subtle"
                        style={{ display: "flex", alignItems: "center", padding: 0 }}
                        onClick={() => setOpenDetail(!openDetail)}
                        color={"dark"}
                    >
                        <Pencil color="#777" size={20} />
                    </Button>
                    <Textarea
                        autosize
                        minRows={1}
                        value={localChoiceTextState}
                        size="xs"
                        style={{ flexBasis: "80%" }}
                        onChange={(e) => setLocalChoiceTextState(e.currentTarget.value)}
                    />
                    <ArrowsSplit2 color="#6993a9" size={24} style={{ margin: "0 5px" }} />
                    <TextInput
                        value={localChoiceNextState}
                        size="sm"
                        style={{ flexBasis: "20%" }}
                        onChange={(e) => setLocalChoiceNextState(e.currentTarget.value)}
                    />
                    <div>
                        <Button
                            compact
                            variant="subtle"
                            style={{ display: "flex", alignItems: "center", padding: 0 }}
                            onClick={() => onDeleteChoice(index)}
                            color={"dark"}
                        >
                            <X color="#777" size={20} />
                        </Button>
                    </div>
                </div>

                <Collapse in={openDetail} style={{ marginTop: 10 }}>
                    <div style={{ display: "flex", gap: 10, alignItems: "flex-start" }}>
                        <Card
                            p={"xs"}
                            sx={(theme) => ({
                                backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[5] : theme.colors.gray[4],
                                minWidth: 48
                            })}
                        >
                            <Group direction="column" spacing={"xs"} style={{ alignItems: "flex-start" }}>
                                <ActionIcon variant={tab === 0 ? "filled" : "default"} onClick={() => setTab(0)} color={"bluegray"} sx={(theme) => ({
                                    color: tab === 0 ? "#fff" : (theme.colorScheme === "dark" ? "#fff" : theme.colors.bluegray[5]),
                                })}>
                                    <Eye />
                                </ActionIcon>
                                <ActionIcon variant={tab === 1 ? "filled" : "default"} onClick={() => setTab(1)} color={"bluegray"} sx={(theme) => ({
                                    color: tab === 1 ? "#fff" : (theme.colorScheme === "dark" ? "#fff" : theme.colors.bluegray[5]),
                                })}>
                                    <HandClick />
                                </ActionIcon>
                                <ActionIcon variant={tab === 2 ? "filled" : "default"} onClick={() => setTab(2)} color={"bluegray"} sx={(theme) => ({
                                    color: tab === 2 ? "#fff" : (theme.colorScheme === "dark" ? "#fff" : theme.colors.bluegray[5]),
                                })}>
                                    <ChartArrowsVertical />
                                </ActionIcon>
                            </Group>
                        </Card>
                        <Card
                            p={"xs"}
                            sx={(theme) => ({
                                backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[5] : theme.colors.gray[4],
                                flexGrow: 1
                            })}
                        >
                            <EventNodeChoiceOptions
                                choiceIndex={index}
                                optionType={
                                    tab === 0 ? "prereq"
                                        : tab === 1 ? "req"
                                            : tab === 2 ? "conseq"
                                                : undefined
                                }
                                options={
                                    tab === 0 ? choice.prereq
                                        : tab === 1 ? choice.req
                                            : tab === 2 ? choice.conseq
                                                : undefined
                                }
                                onChangeChoiceOptionContent={onChangeChoiceOptionContent}
                                onAddChoiceOption={onAddChoiceOption}
                                onDeleteChoiceOption={onDeleteChoiceOption}
                            />
                        </Card>
                    </div>
                </Collapse>
            </Group>
        </Card>
    )
}

const EventNodeChoiceOptions = ({ choiceIndex, optionType, options, operators, onChangeChoiceOptionContent, onAddChoiceOption, onDeleteChoiceOption }) => {
    return (
        <>
            {
                options && options.map((option, optionIndex) =>
                    <EventNodeChoiceOptionsEntry
                        optionType={optionType} option={option} key={`choice-option-${choiceIndex}-${optionType}-${optionIndex}`} choiceIndex={choiceIndex} optionIndex={optionIndex} operators={operators}
                        onChangeChoiceOptionContent={onChangeChoiceOptionContent}
                        onAddChoiceOption={onAddChoiceOption}
                        onDeleteChoiceOption={onDeleteChoiceOption}
                    />
                )
            }
            <Button fullWidth variant="light" compact color={"bluegray"} style={{ marginTop: options && options.length > 0 ? 10 : 0 }} onClick={() => onAddChoiceOption(choiceIndex, optionType)}>+</Button>
        </>
    )
}

const EventNodeChoiceOptionsEntry = ({ choiceIndex, optionType, option, optionIndex, operators, onChangeChoiceOptionContent, onDeleteChoiceOption }) => {

    const [localChoiceOptionDomainState, setLocalChoiceOptionDomainState] = useState(option?.domain);
    const [localChoiceOptionAttrState, setLocalChoiceOptionAttrState] = useState(option?.attr);
    const [localChoiceOptionOperatorState, setLocalChoiceOptionOperatorState] = useState(option?.op);
    const [localChoiceOptionValueState, setLocalChoiceOptionValueState] = useState(option?.value);
    const [dChoiceOptionDomainState] = useDebouncedValue(localChoiceOptionDomainState, 200);
    const [dChoiceOptionAttrState] = useDebouncedValue(localChoiceOptionAttrState, 200);
    const [dChoiceOptionOperatorState] = useDebouncedValue(localChoiceOptionOperatorState, 200);
    const [dChoiceOptionValueState] = useDebouncedValue(localChoiceOptionValueState, 200);

    useEffect(() => {
        if (!dChoiceOptionDomainState || dChoiceOptionDomainState === option.domain) return;
        onChangeChoiceOptionContent(choiceIndex, optionType, optionIndex, "domain", dChoiceOptionDomainState);
    }, [dChoiceOptionDomainState]);

    useEffect(() => {
        if (!dChoiceOptionAttrState || dChoiceOptionAttrState === option.attr) return;
        onChangeChoiceOptionContent(choiceIndex, optionType, optionIndex, "attr", dChoiceOptionAttrState);
    }, [dChoiceOptionAttrState]);

    useEffect(() => {
        if (!dChoiceOptionOperatorState || dChoiceOptionOperatorState === option.op) return;
        onChangeChoiceOptionContent(choiceIndex, optionType, optionIndex, "op", dChoiceOptionOperatorState);
    }, [dChoiceOptionOperatorState]);

    useEffect(() => {
        if (!dChoiceOptionValueState || dChoiceOptionValueState === option.value) return;

        // Check if the value is a number
        if (/^-?\d+$/.test(dChoiceOptionValueState)) {
            onChangeChoiceOptionContent(choiceIndex, optionType, optionIndex, "value", parseInt(dChoiceOptionValueState));
        }
        else {
            showNotification({ message: "Value must be a number", color: "red" });
        }
    }, [dChoiceOptionValueState]);


    useEffect(() => {
        if (!option?.domain) return;
        setLocalChoiceOptionDomainState(option.domain);
    }, [option.domain])

    useEffect(() => {
        if (!option?.attr) return;
        setLocalChoiceOptionAttrState(option.attr);
    }, [option.attr])

    useEffect(() => {
        if (!option?.op) return;
        setLocalChoiceOptionOperatorState(option.op);
    }, [option.op])

    useEffect(() => {
        if (!option?.value) return;
        setLocalChoiceOptionValueState(option.value);
    }, [option.value])

    return (
        <Card
            p={"xs"}
            sx={(theme) => ({
                marginTop: 5,
                backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[6] : theme.colors.gray[5],
            })}
        >
            <div style={{ display: "flex", width: "100%", gap: 10, alignItems: "center" }}>
                <Autocomplete size="xs" value={localChoiceOptionDomainState} data={["a", "b", "c"]} style={{ flexBasis: "35%" }} onChange={setLocalChoiceOptionDomainState} />
                <Autocomplete size="xs" value={localChoiceOptionAttrState} data={[]} style={{ flexBasis: "35%" }} onChange={setLocalChoiceOptionAttrState} />
                <Autocomplete size="xs" value={localChoiceOptionOperatorState} data={[]} style={{ flexBasis: "10%" }} onChange={setLocalChoiceOptionOperatorState} />
                <Autocomplete size="xs" value={localChoiceOptionValueState?.toString()} data={[]} style={{ flexBasis: "15%" }} onChange={setLocalChoiceOptionValueState} />
                <ActionIcon size={"xs"} style={{ flexBasis: "5%", height: "100%" }} >
                    <X onClick={() => onDeleteChoiceOption(choiceIndex, optionType, optionIndex)} />
                </ActionIcon>
            </div>
        </Card>
    )
}