import { ActionIcon, Autocomplete, Button, Card, Collapse, Group, LoadingOverlay, ScrollArea, Textarea } from "@mantine/core";
import { useDebouncedValue } from "@mantine/hooks";
import { useContext, useEffect, useState } from "react";
import { Draggable } from "react-beautiful-dnd";
import { Droppable } from "react-beautiful-dnd";
import { DragDropContext } from "react-beautiful-dnd";
import { Eye, GripVertical, Pencil, X } from "tabler-icons-react";
import { EventStateContext } from "../EventProvider";
import { CustomNodeWarningLoader } from "./EditEventNodeSection";

const newTextTemplate = () => ({
    text: "",
});

export const EventNodeTextsTab = () => {

    const eventContext = useContext(EventStateContext);

    const reorder = (list, startIndex, endIndex) => {
        const result = Array.from(list);
        const [removed] = result.splice(startIndex, 1);
        result.splice(endIndex, 0, removed);

        return result;
    };

    function onDragEnd(result) {
        if (!result.destination) {
            return;
        }

        if (result.destination.index === result.source.index) {
            return;
        }

        const reorderedTexts = reorder(
            eventContext.currentEventNode.texts,
            result.source.index,
            result.destination.index
        );

        eventContext.onChangeEventNodeText(reorderedTexts);
    }

    const onChangeText = (index, text) => {
        let buffer = eventContext.currentEventNode.texts;
        buffer[index].text = text;
        eventContext.onChangeEventNodeText(buffer);
    }

    const onDeleteText = (index) => {
        let buffer = eventContext.currentEventNode.texts;
        buffer.splice(index, 1);
        eventContext.onChangeEventNodeText(buffer);
    }

    const addNewText = () => {
        let buffer = eventContext.currentEventNode.texts;
        buffer.push(newTextTemplate());
        eventContext.onChangeEventNodeText(buffer);
    }

    return (
        <Card shadow="sm" p="lg" withBorder style={{ height: "100%", flexBasis: "50%" }}>
            <Card.Section
                sx={(theme) => ({
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.bluegray[9] : theme.colors.bluegray[4]
                })}
            >
                <div style={{ height: 40, display: "flex", justifyContent: "center", alignItems: "center", color: "white", letterSpacing: 1 }}>
                    NODE TEXTS
                </div>
            </Card.Section>
            <div style={{ position: "relative", height: "100%" }}>
                <ScrollArea offsetScrollbars style={{ position: "absolute", top: 20, bottom: 20, left: 10, right: 10 }}>
                    <DragDropContext onDragEnd={onDragEnd} >
                        <Droppable droppableId="node-text">
                            {provided => (
                                <div
                                    ref={provided.innerRef}
                                    {...provided.droppableProps}
                                >
                                    <EventNodeTextsList
                                        texts={eventContext.currentEventNode?.texts} onChange={onChangeText} onDelete={onDeleteText}
                                        onChangeTextOptionContent={eventContext.onChangeTextOptionContent} onAddTextOption={eventContext.onAddTextOption} onDeleteTextOption={eventContext.onDeleteTextOption}
                                    />
                                    {provided.placeholder}
                                </div>
                            )}
                        </Droppable>
                        <Button fullWidth variant="light" compact color={"bluegray"} onClick={addNewText}>+</Button>
                    </DragDropContext>
                </ScrollArea>
            </div>

            <LoadingOverlay loader={<CustomNodeWarningLoader />} visible={!eventContext.currentEventNode} />
        </Card>
    )
}

const EventNodeTextsList = ({ texts, onChange, onDelete, onChangeTextOptionContent, onAddTextOption, onDeleteTextOption }) => (
    <>
        {
            texts && texts.map((text, index) => {
                return <EventNodeTextEntry
                    draggableId={`node-text-${index}`} index={index} text={text} onChange={onChange} onDelete={onDelete} key={`node-text-${index}`}
                    onChangeTextOptionContent={onChangeTextOptionContent} onAddTextOption={onAddTextOption} onDeleteTextOption={onDeleteTextOption}
                />
            })
        }
    </>
)

const EventNodeTextEntry = ({ text, index, draggableId, onChange, onDelete, onChangeTextOptionContent, onAddTextOption, onDeleteTextOption }) => {
    const [localTextState, setLocalTextState] = useState();
    const [dTextState] = useDebouncedValue(localTextState, 200);

    const [openDetail, setOpenDetail] = useState(false);
    const [tab, setTab] = useState(0);

    useEffect(() => {
        if (!dTextState || dTextState === text.text) return;
        onChange(index, dTextState);
    }, [dTextState]);

    useEffect(() => {
        if (!text?.text) return;
        setLocalTextState(text.text);
    }, [text.text])

    return (
        <Draggable draggableId={draggableId} index={index} style={{ margin: 5 }}>
            {provided => (
                <Card
                    p={"xs"}
                    withBorder
                    sx={(theme) => ({
                        backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[4] : theme.colors.gray[2],
                        marginBottom: 5
                    })}
                    ref={provided.innerRef}
                    {...provided.draggableProps}

                >
                    <div style={{ display: "flex", gap: 4, alignItems: "center" }}>
                        <div
                            {...provided.dragHandleProps}
                            style={{ display: "flex", alignItems: "center", marginLeft: -4 }}
                        >
                            <GripVertical color="#777" />
                        </div>
                        <Button
                            compact
                            variant="subtle"
                            style={{ display: "flex", alignItems: "center", padding: 0 }}
                            onClick={() => setOpenDetail(!openDetail)}
                            color={"dark"}
                        >
                            <Pencil color="#777" size={20} />
                        </Button>
                        <Textarea
                            autosize
                            minRows={1}
                            value={localTextState}
                            size="xs"
                            style={{ flexGrow: 1 }}
                            onChange={(e) => setLocalTextState(e.currentTarget.value)}
                        />
                        <div>
                            <Button
                                compact
                                variant="subtle"
                                style={{ display: "flex", alignItems: "center", padding: 0 }}
                                onClick={() => onDelete(index)}
                                color={"dark"}
                            >
                                <X color="#777" size={20} />
                            </Button>
                        </div>
                    </div>

                    <Collapse in={openDetail} style={{ marginTop: 10 }}>
                        <div style={{ display: "flex", gap: 10, alignItems: "flex-start" }}>
                            <Card
                                p={"xs"}
                                sx={(theme) => ({
                                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[5] : theme.colors.gray[4],
                                    minWidth: 48
                                })}
                            >
                                <Group direction="column" spacing={"xs"} style={{ alignItems: "flex-start" }}>
                                    <ActionIcon variant={tab === 0 ? "filled" : "default"} onClick={() => setTab(0)} color={"bluegray"} sx={(theme) => ({
                                        color: tab === 0 ? "#fff" : (theme.colorScheme === "dark" ? "#fff" : theme.colors.bluegray[5]),
                                    })}>
                                        <Eye />
                                    </ActionIcon>
                                </Group>
                            </Card>
                            <Card
                                p={"xs"}
                                sx={(theme) => ({
                                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[5] : theme.colors.gray[4],
                                    flexGrow: 1
                                })}
                            >
                                <EventNodeTextOptions
                                    textIndex={index}
                                    optionType={
                                        tab === 0 ? "prereq"
                                            : undefined
                                    }
                                    options={
                                        tab === 0 ? text.prereq
                                            : undefined
                                    }
                                    onChangeTextOptionContent={onChangeTextOptionContent}
                                    onAddTextOption={onAddTextOption}
                                    onDeleteTextOption={onDeleteTextOption}
                                />
                            </Card>
                        </div>
                    </Collapse>
                </Card>
            )}
        </Draggable>

    )
}

const EventNodeTextOptions = ({ textIndex, optionType, options, operators, onChangeTextOptionContent, onAddTextOption, onDeleteTextOption }) => {
    return (
        <>
            {
                options && options.map((option, optionIndex) =>
                    <EventNodeTextOptionsEntry
                        optionType={optionType} option={option} key={`text-option-${textIndex}-${optionType}-${optionIndex}`} textIndex={textIndex} optionIndex={optionIndex} operators={operators}
                        onChangeTextOptionContent={onChangeTextOptionContent}
                        onAddTextOption={onAddTextOption}
                        onDeleteTextOption={onDeleteTextOption}
                    />
                )
            }
            <Button fullWidth variant="light" compact color={"bluegray"} style={{ marginTop: options && options.length > 0 ? 10 : 0 }} onClick={() => onAddTextOption(textIndex, optionType)}>+</Button>
        </>
    )
}

const EventNodeTextOptionsEntry = ({ textIndex, optionType, option, optionIndex, operators, onChangeTextOptionContent, onDeleteTextOption }) => {

    const [localTextOptionDomainState, setLocalTextOptionDomainState] = useState(option?.domain);
    const [localTextOptionAttrState, setLocalTextOptionAttrState] = useState(option?.attr);
    const [localTextOptionOperatorState, setLocalTextOptionOperatorState] = useState(option?.op);
    const [localTextOptionValueState, setLocalTextOptionValueState] = useState(option?.value);
    const [dTextOptionDomainState] = useDebouncedValue(localTextOptionDomainState, 200);
    const [dTextOptionAttrState] = useDebouncedValue(localTextOptionAttrState, 200);
    const [dTextOptionOperatorState] = useDebouncedValue(localTextOptionOperatorState, 200);
    const [dTextOptionValueState] = useDebouncedValue(localTextOptionValueState, 200);

    useEffect(() => {
        if (!dTextOptionDomainState || dTextOptionDomainState === option.domain) return;
        onChangeTextOptionContent(textIndex, optionType, optionIndex, "domain", dTextOptionDomainState);
    }, [dTextOptionDomainState]);

    useEffect(() => {
        if (!dTextOptionAttrState || dTextOptionAttrState === option.attr) return;
        onChangeTextOptionContent(textIndex, optionType, optionIndex, "attr", dTextOptionAttrState);
    }, [dTextOptionAttrState]);

    useEffect(() => {
        if (!dTextOptionOperatorState || dTextOptionOperatorState === option.op) return;
        onChangeTextOptionContent(textIndex, optionType, optionIndex, "op", dTextOptionOperatorState);
    }, [dTextOptionOperatorState]);

    useEffect(() => {
        if (!dTextOptionValueState || dTextOptionValueState === option.value) return;
        onChangeTextOptionContent(textIndex, optionType, optionIndex, "value", dTextOptionValueState);
    }, [dTextOptionValueState]);


    useEffect(() => {
        if (!option?.domain) return;
        setLocalTextOptionDomainState(option.domain);
    }, [option.domain])

    useEffect(() => {
        if (!option?.attr) return;
        setLocalTextOptionAttrState(option.attr);
    }, [option.attr])

    useEffect(() => {
        if (!option?.op) return;
        setLocalTextOptionOperatorState(option.op);
    }, [option.op])

    useEffect(() => {
        if (!option?.value) return;
        setLocalTextOptionValueState(option.value);
    }, [option.value])

    return (
        <Card
            p={"xs"}
            sx={(theme) => ({
                marginTop: 5,
                backgroundColor: theme.colorScheme === "dark" ? theme.colors.dark[6] : theme.colors.gray[5],
            })}
        >
            <div style={{ display: "flex", width: "100%", gap: 10, alignItems: "center" }}>
                <Autocomplete size="xs" value={localTextOptionDomainState} data={["a", "b", "c"]} style={{ flexBasis: "35%" }} onChange={setLocalTextOptionDomainState} />
                <Autocomplete size="xs" value={localTextOptionAttrState} data={[]} style={{ flexBasis: "35%" }} onChange={setLocalTextOptionAttrState} />
                <Autocomplete size="xs" value={localTextOptionOperatorState} data={[]} style={{ flexBasis: "10%" }} onChange={setLocalTextOptionOperatorState} />
                <Autocomplete size="xs" value={localTextOptionValueState?.toString()} data={[]} style={{ flexBasis: "15%" }} onChange={setLocalTextOptionValueState} />
                <ActionIcon size={"xs"} style={{ flexBasis: "5%", height: "100%" }} >
                    <X onClick={() => onDeleteTextOption(textIndex, optionType, optionIndex)} />
                </ActionIcon>
            </div>
        </Card>
    )
}