import { Card, Group, LoadingOverlay, TextInput } from "@mantine/core";
import { useDebouncedValue } from "@mantine/hooks";
import { useContext, useEffect, useState } from "react";
import { EventStateContext } from "../EventProvider";
import { CustomNodeWarningLoader } from "./EditEventNodeSection";

export const EventMetadataTab = () => {

    const eventContext = useContext(EventStateContext);

    const [localEventNameState, setLocalEventNameState] = useState();
    const [localEventEntryState, setLocalEventEntryState] = useState();
    const [dEventNameState] = useDebouncedValue(localEventNameState, 200);
    const [dEventEntryTextState] = useDebouncedValue(localEventEntryState, 200);

    useEffect(() => {
        if (!dEventNameState) return;
        eventContext.onChangeEventName(dEventNameState)
    }, [dEventNameState]);


    useEffect(() => {
        if (!dEventEntryTextState) return;
        eventContext.onChangeEventEntry(dEventEntryTextState)
    }, [dEventEntryTextState]);

    useEffect(() => {
        setLocalEventNameState(eventContext.currentEvent?.name);
    }, [eventContext.currentEvent?.name])

    useEffect(() => {
        setLocalEventEntryState(eventContext.currentEvent?.entry);
    }, [eventContext.currentEvent?.entry])

    return (
        <Card shadow="sm" p="sm" withBorder style={{ height: "100%", flexBasis: "50%" }}>
            <Card.Section
                sx={(theme) => ({
                    backgroundColor: theme.colorScheme === "dark" ? theme.colors.bluegray[9] : theme.colors.bluegray[4]
                })}
            >
                <div style={{ height: 40, display: "flex", justifyContent: "center", alignItems: "center", color: "white", letterSpacing: 1 }}>
                    EVENT METADATA
                </div>
            </Card.Section>
            <Group grow style={{ height: "100%" }}>
                <TextInput label={"Event ID"} size="xs" value={eventContext.currentEvent?.entry.split("_")[0]} disabled />
                <TextInput label={"Event Name"} size="xs" value={localEventNameState} onChange={(e) => setLocalEventNameState(e.target.value)} />
                <TextInput label={"Entry Point"} size="xs" value={localEventEntryState} onChange={(e) => setLocalEventEntryState(e.target.value)} />
            </Group>

            <LoadingOverlay loader={<CustomNodeWarningLoader isEvent />} visible={!eventContext.currentEvent} />
        </Card >
    )
}